'use client'

import { IContacts } from "@/type/Types";

interface ContactsClientProps {
    contacts: IContacts[]
}

export default function ContactsClient({contacts}: ContactsClientProps) {
    return (
        <div>
            {contacts.map((contact, index) => (
                <div key={index}> 
                    <h2>{contact.name}</h2>
                    <p>{contact.email}</p>
                </div>
            ))}
        </div>
    );
}